import React from 'react'
import { useHistory } from "react-router-dom";


const Navbar = () => {
    let history = useHistory();
    const handleLogout=(e)=>{
        e.preventDefault()
        localStorage.removeItem('Bearer')
       history.push('/')
    }

    return (
        <div>
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <a className="navbar-brand" href="#">URL Shortner</a>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarNav">
    <ul className="navbar-nav">
      <li className="nav-item active">
        <a className="nav-link" href="/home">Home <span className="sr-only">(current)</span></a>
      </li>
      <li className="nav-item">
        <a className="nav-link" href="/dashboard">All Urls</a>
      </li>
      <li className="nav-item">
        <a className="nav-link" href="/urlshorten">Url shorten</a>
      </li>
    </ul>   
    <ul className="navbar-nav">
    <li className="nav-item" style={{paddingLeft:'1000px'}}>
        <a className="nav-link" href='#' onClick={handleLogout} >Log Out</a>
      </li>
      </ul>
  </div>
</nav>
        </div>
    )
}

export default Navbar
